*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
ul,li{
    list-style: none;
}
a{
    display: inline-block;
    width: 100%;
    height: 100%;
    color: #fff;
    text-decoration: none;
}
a:hover{
    color: #fff;
}
img{
    width: 100%;
}

.img{
    border-radius: 50%;
    overflow: hidden;
    border: 5px solid #e4e9fa;
}
.img.fat{
    border-color: #E33D64;
}
.img.death{
    border-color: #000;
}
.wrap{
    min-height: 100vh;
    text-align: center;
    background: #f1f3fa;
}
.header{
    height: 80px;
}
.header h1{
    padding-top: 20px;
    font-size: 30px;
    color: #5b608c;
}
.content{
    padding: 100px 0 50px;
}
.content_wrap{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
}
.button__wrap{
    display: flex;
    justify-content: center;
    margin-top: 20px;
}
/*profile*/
.profile{
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 15px;
    padding: 30px 0;
    min-width: 300px;
    border-radius: 10px;
    background: #fff;
    -webkit-box-shadow: 3px 3px 5px 0 rgba(0,0,0,0.1);
    box-shadow: 3px 3px 5px 0 rgba(0,0,0,0.1);
}
.profile_img{
    width: 160px;
    height: 150px;
}
.profile_name{
    margin-top: 10px;
    font-size: 22px;
    font-weight: bold;
    color: #5b608c;
}
.profile_gender{
    font-size: 12px;
    color: #a1adb7;
}
.profile_state{
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 10px 0 0;
}
.profile_state li{
    width: 50px;
    margin-left: 15px;
}
.profile_state li:first-child{
    margin-left: 0;
}
.state_number{
    font-size: 18px;
    font-weight: bold;
    color: #5b608c;
}
.state_name{
    font-size: 14px;
    color: #8f9da9;
}

/*derail*/
.detail{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    padding: 40px 30px 50px;
    width: 70%;
    background-color: #fff;
    border-radius: 20px;
}
.detail_img{
    width: 300px;
    height: 300px;
}
.detail_text{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 15px;
}
.detail_name{
    font-size: 24px;
    font-weight: bold;
    color: #5b608c;
}

.detail_info{
    margin-left: 50px;
    width: 60%;
}
.detail_info .profile_state{
    padding: 20px 0;
    background: #5b608c;
    border-radius: 10px 10px 0 0;
}
.detail_info .profile_state li{
    width: 33.33%;
    border-left: 1px solid #e4e9fa;
}
.detail_info .profile_state li:first-child{
    border: none;
}
.detail_info .state_number{
    color: #fff;
}
.detail_info .state_name{
    color: #eee;
}

.add_text{
    display: flex;
    padding: 20px;
    height: 200px;
    overflow-x: hidden;
    overflow-y: auto;
    background: #f1f3fa;
    word-break: break-all;
    border-radius: 0 0 10px 10px;
    text-align: left;
}
.add_text::-webkit-scrollbar{
    width: 5px;
}
.add_text::-webkit-scrollbar-thumb{
    background-color: #5b608c;
    border-radius: 2px;
}
.add_text::-webkit-scrollbar-track{
    background-color: #e4e9fa;
}
.add_text div{
    width: 50%;
}
.message h4{
    margin-bottom: 10px;
    font-size: 18px;
    color: #5b608c;
}
.add_text ul li h4{
    font-size: 18px;
}
.add_text li{
    font-size: 14px;
    font-weight: bold;
    line-height: 1.5;
    color: #5b608c;
}
.add_text li span {
    display: inline-block;
    margin-right: 7px;
    min-width: 15px;
    font-size: 14px;
    text-align: center;
}
.time_section{
    margin-bottom: 10px;
}

/*login*/
.login {
    display: flex;
    justify-content: center;
    padding: 50px 30px;
    width: 500px;
    background-color: #e4e9fa;
    border-radius: 20px;
}
.login h2{
    font-size: 30px;
    color: #5b608c;
}
.login-form{
    display: flex;
    flex-direction: column;
}
.login-form input {
    margin-top: 15px;
    padding: 5px 10px;
    max-width: 300px;
    font-size: 14px;
    border-radius: 5px;
    border: none;
}
/*timer*/
.timer{
    display: none;
    position: absolute;
    top: -35px;
    left: 50%;
    padding: 3px 10px;
    border-radius: 30%;
    background-color: #5b608c;
    transform: translateX(-50%);
}
.timer__wrap{
    position: relative;
}
.timer.block{
    display: block;
}
.timer__text{
    position: relative;
    display: inline-block;
    color: #fff;
    font-size: 12px;
}
.timer__text::after{
    position: absolute;
    bottom: -16px;
    left: 50%;
    z-index: 10;
    width: 0;
    height: 0;
    border-top: 8px solid #5b608c;
    border-right: 8px solid transparent;
    border-bottom: 8px solid transparent;
    border-left: 8px solid transparent;
    transform: translateX(-50%);
    content: '';
}
@media (max-width: 1050px){
    .content{
        padding: 50px 0;
    }
    .detail_info{
        margin-left: 0;
        width: 100%;
    }
    .detail_img{
        width: 250px;
        height: 250px;
    }
}
@media (max-width: 500px){
    .content{
        padding: 30px 0;
    }
    .detail{
        padding: 30px 15px 30px;
        width: 80%;
    }
    .detail_img{
        width: 200px;
        height: 200px;
    }
    .state_number{
        font-size: 14px;
    }
    .add_text{
        padding: 10px;
    }
}